.table {

  & thead > tr > th,
  & tbody > tr > td {
    padding: 5px;
    text-align: left;
  }
  & thead {
    border: 1px solid #c9e6f2;
    background-color: #f2f9fc;

  }
  & tbody {
    & tr {
      border-left: 1px solid #eee;
      border-right: 1px solid #eee;
      border-bottom: 1px solid #eee;
    }
  }
  &.table-hover tbody tr:hover {
background: #f5f5f5;
  }
}
